// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'

import * as Sentry from '@sentry/browser'
import * as Integrations from '@sentry/integrations'

// 错误监控
Sentry.init({
  dsn: 'https://22985ed475144f804f22237db840d3d5@o4507254030073856.ingest.us.sentry.io/4507254041346048',
  release: 'test@2.1.2',
  integrations: [new Integrations.Vue({Vue, attachProps: true})],
  autoSessionTracking: true,
  logErrors: true,
  tracesSampleRate: 1.0,
  beforeSend (event) {
    if (event && event.exception && event.exception.values && event.exception.values[0] && event.exception.values[0].type === 'UnhandledRejection') {
      return null
    }
    return event
  }
})

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



