import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/user',
      name: 'User',
      component: () => import('@/components/User')
    },
    {
      path: '/files',
      name: 'Files',
      component: () => import('@/components/Files')
    }
  ]
})



